import React, { useEffect, useState } from "react";
import { Box, Stack, Typography, IconButton, Tooltip } from "@mui/material";
import MicIcon from '@mui/icons-material/Mic';

import { fetchFromAPI } from "../utils/fetchFromAPI";
import { Videos, Sidebar, ShortsPlayer, VoiceControl } from "./";
import { categories } from "../utils/constants";

const Feed = () => {
  const [selectedCategory, setSelectedCategory] = useState("New");
  const [videos, setVideos] = useState(null);
  const [isListening, setIsListening] = useState(false);

  const toggleListening = () => {
    setIsListening(!isListening);
  };

  useEffect(() => {
    setVideos(null);

    const fetchVideos = async () => {
      let data;
      const selectedCategoryObj = categories.find(cat => cat.name === selectedCategory);
      if (selectedCategory === "New") {
        const categories = ['new movie trailer', 'new music video', 'educational content', 'trending videos'];
        const promises = categories.map(category => 
          fetchFromAPI(`search?part=snippet&q=${category}&order=date&regionCode=IN&type=video&maxResults=12`)
        );
        const results = await Promise.all(promises);
        data = { items: results.flatMap(result => result.items) };
      } else if (selectedCategory === "Shorts") {
        data = await fetchFromAPI(`search?part=snippet&q=${selectedCategoryObj.query}&order=date&regionCode=IN&type=video&videoDuration=short&maxResults=50`);
      } else if (selectedCategory === "Movies") {
        data = await fetchFromAPI(`search?part=snippet&q=${selectedCategoryObj.query}&order=date&regionCode=IN&type=video&videoDuration=long&maxResults=50`);
      } else {
        data = await fetchFromAPI(`search?part=snippet&q=${selectedCategory} indian&regionCode=IN&type=video&maxResults=50`);
      }
      setVideos(data.items);
    };

    fetchVideos();
  }, [selectedCategory]);

  const handleVoiceCommand = (command) => {
    if (command.includes('play shorts')) {
      setSelectedCategory('Shorts');
    } else if (command.includes('show new')) {
      setSelectedCategory('New');
    } else if (command.includes('show movies')) {
      setSelectedCategory('Movies');
    }
    // Add more voice commands as needed
  };

  return (
    <Stack sx={{ flexDirection: { sx: "column", md: "row" } }}>
      <Box sx={{ height: { sx: "auto", md: "92vh" }, borderRight: "1px solid #3d3d3d", px: { sx: 0, md: 2 } }}>
        <Sidebar
          selectedCategory={selectedCategory}
          setSelectedCategory={setSelectedCategory}
          categories={categories}
        />
        
        <Typography className="copyright" variant="body2" sx={{ mt: 1.5, color: "#fff" }}>
          Copyright © 2024 <a href="https://gauravkumaryadav.great-site.net/?i=1" target="_blank" rel="noopener noreferrer" style={{ color: '#FC1503', textDecoration: 'none' }}>FlyingDarkDev</a>
        </Typography>
        
      </Box>

      <Box p={2} sx={{ overflowY: "auto", height: "90vh", flex: 2 }}>
        <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
          <Typography variant="h4" fontWeight="bold" sx={{ color: "white" }}>
            {selectedCategory === "New" ? "New & Trending" : selectedCategory} <span style={{ color: "#FC1503" }}>videos</span>
          </Typography>
          <VoiceControl
            isListening={isListening}
            toggleListening={toggleListening}
            onCommand={handleVoiceCommand}
          />
        </Box>

        {selectedCategory === "Shorts" ? (
          <ShortsPlayer videos={videos} />
        ) : (
          <Videos videos={videos} />
        )}
      </Box>
    </Stack>
  );
};

export default Feed;