import React, { useState } from 'react';
import { Box, IconButton, Typography } from '@mui/material';
import { SkipPrevious, SkipNext } from '@mui/icons-material';
import ReactPlayer from 'react-player';

const ShortsPlayer = ({ videos }) => {
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);

  const handleNext = () => {
    setCurrentVideoIndex((prevIndex) => (prevIndex + 1) % videos.length);
  };

  const handlePrevious = () => {
    setCurrentVideoIndex((prevIndex) => (prevIndex - 1 + videos.length) % videos.length);
  };

  if (!videos || videos.length === 0) return null;

  const currentVideo = videos[currentVideoIndex];

  return (
    <Box sx={{ position: 'relative', width: '100%', height: '80vh' }}>
      <ReactPlayer
        url={`https://www.youtube.com/watch?v=${currentVideo.id.videoId}`}
        width="100%"
        height="100%"
        playing
        controls
        loop
      />
      <Box sx={{ position: 'absolute', bottom: 16, left: 16, right: 16, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <IconButton onClick={handlePrevious} sx={{ color: 'white' }}>
          <SkipPrevious />
        </IconButton>
        <Typography variant="body1" sx={{ color: 'white' }}>
          {currentVideo.snippet.title}
        </Typography>
        <IconButton onClick={handleNext} sx={{ color: 'white' }}>
          <SkipNext />
        </IconButton>
      </Box>
    </Box>
  );
};

export default ShortsPlayer;